html {
  box-sizing: border-box;
}


*, *::before *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  /* Background pattern from Subtle Patterns */
  background: url(/portfolio/static/media/ep_naturalblack.23e2b6cf.png);
  color: #eee;
}
.App {
    height: 100vh;
}

/* Position and sizing of burger button */
.bm-burger-button {
position: fixed;
width: 36px;
height: 30px;
left: 36px;
top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
background: #eee;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
height: 24px;
width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
background: #373a47;
padding: 2.5em 1.5em 0;
font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
color: #b8b7ad;
padding: 0.8em;
}

/* Individual item */
.bm-item {
display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
background: rgba(0, 0, 0, 0.3);
}

.jumbotron {
    margin-top: 5.5em;
    background: none;
}

.person-wrapper {
    margin: 1em;
}

.profile-pic {
    width: 20%;
}

.des {
    font-size: 16px;
}

.btn-primary {
    background:#eee;
    border: 1px solid rgb(0, 0, 0);
    color: rgb(0, 0, 0);
    border-radius: .30rem;
    cursor: pointer;
}

.icon {
    width: 18%;
}

.footer {
    margin-top: 4em;
}

@media screen and (min-width: 320px) and (max-width: 1024px){
    .icon {
        width: 40%;
        margin-bottom: 0.5em;
    }

}
